<template>
  <div class="busqueda__contenedor">
    <input
      type="search"
      class="busqueda__input"
      placeholder="Buscar"
      @keyup.enter="armarbusqueda"
      v-model="busqueda"
    />
    <!--en la linea 7 falta: 
    @blur="armarbusqueda"
    -->
    <div class="busqueda__identificacion">
      <div class="busqueda__identificacion__icono">
        <i class="fas fa-user"></i>
      </div>
      <div class="busqueda__identificacion__datos">Mario Lecca Rosales</div>
    </div>
  </div>
</template>

<script>
import { EventBus } from "./event-bus";
export default {
  name: "FuBusqueda",

  data() {
    return {
      buscarpor: "track",
      busqueda: "",
      resultadosdebusqueda: [],
    };
  },
  methods: {
    async armarbusqueda() {
      //https://api.deezer.com/search/track?q=artist:%22aloe%20blacc%22
      await fetch(
        "https://deezerdevs-deezer.p.rapidapi.com/search/track?q=" +
          this.buscarpor +
          ':"' +
          this.busqueda +
          '"',
        {
          method: "GET",
          headers: {
            "x-rapidapi-key":
              "19a9142c6emsh91209d66b6f4e5dp1107cfjsne54690d64d38",
            "x-rapidapi-host": "deezerdevs-deezer.p.rapidapi.com",
          },
        }
      )
        .then((response) => response.json())
        .then((data) => {
          this.resultadosdebusqueda = data;
        })
        /*.then((resultadosdebusqueda) => {
          EventBus.$emit(
            'nuevosresultadosdebusqueda',
            resultadosdebusqueda
          );
        })*/
        .catch((err) => {
          console.error(err);
        });
      //console.log(this.resultadosdebusqueda);
      EventBus.$emit('nuevosresultadosdebusqueda',this.resultadosdebusqueda);
    },

    actualizartipodebusqueda(nuevotipodebusqueda) {
      this.buscarpor = nuevotipodebusqueda;
    },
  },
  mounted() {
    EventBus.$on('cambiodeseleccionenelmenu', (nuevotipodebusqueda) =>
      this.actualizartipodebusqueda(nuevotipodebusqueda)
    );
  },
  beforeDestroy() {
    EventBus.$off("cambiodeseleccionenelmenu", (nuevotipodebusqueda) =>
      this.actualizartipodebusqueda(nuevotipodebusqueda)
    );
  },
};
</script>

<style scope>
.busqueda__contenedor {
  position: fixed;
  background: #fff;
  height: 80px;
}

.busqueda__input {
  display: inline-flex;
  float: left;
  background-image: url("../assets/imagenes/search-solid.svg");
  background-repeat: no-repeat;
  background-position: 95%;
  background-size: 18px;
  border: 1px solid #828282;
  border-radius: 100px;
  height: 36px;
  width: 54%;
  margin-left: 40px;
  margin-top: 22px;
  text-decoration: inherit;
  font-size: 1.125em;
  font-weight: 200;
  color: #828282;
  text-indent: 20px;
}
.busqueda__identificacion {
  display: inline-flex;
  margin-right: 40px;
  margin-top: 22px;
  height: 36px;
  width: 16%;
  float: right;
}
.busqueda__identificacion__icono {
  display: inline-flex;
  width: 11%;
  height: 14px;
  float: left;
  align-content: center;
  object-fit: cover;
  margin-top: 11px;
  color: #e86060;
}
.busqueda__identificacion__datos {
  display: inline-flex;
  width: 78%;
  height: 20px;
  text-align: right;
  margin-top: 11px;
  margin-left: 12px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;

  font-family: Quicksand;
  font-style: normal;
  font-weight: normal;
  font-size: 1em;
  line-height: 20px;

  color: #4f4f4f;
}
input:focus,
input.form-control :focus {
  outline: none !important;
  outline-width: 0 !important;
  box-shadow: none;
  -moz-box-shadow: none;
  -webkit-box-shadow: none;
  caret-color: #bdbdbd;
}
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  -webkit-appearance: none;
}
@media screen and (min-width: 701px) {
  /*VISTA PC*/
  .busqueda__contenedor {
    position: fixed;
    top: 0px;
    right: 0px;
    width: 75%;
  }
}
@media screen and (max-width: 700px) {
  /*VISTA MOVIL*/
  .busqueda__contenedor {
    position: fixed;
    top: 60px;
    height: 56px;
    right: 0px;
    width: 100%;
  }
  .busqueda__identificacion {
    display: none;
  }
  .busqueda__input {
    width: 91%;
    margin-left: 4.5%;
    margin-top: 10px;
  }
}
</style>