<template>
  <div class="detalle__contenedor">
    <div class="detalle__informacion__fondo_color">
      <img
        :src="artistafoto"
        :alt="artistanombre"
        class="detalle__informacion__fondo_img"
      />
    </div>
    <div class="detalle__informacion">
      <div>
        <img class="detalle__informacion__imagen" :src="albumportada" />
      </div>
      <div class="detalle__informacion__descripcion">
        <p class="album">{{ tituloCancion }}</p>
        <p class="comentario">{{ albumtitulo }}</p>
        <p class="seguidores">{{ artistanombre }}</p>
        <div class="biografia">
          <p>{{ comentario }}</p>
        </div>
      </div>
      <div class="detalle__informacion__botones">
        <a :href="fullplayURL" target="blank" send_referrer="false"><div id="reproducir">Reproducir</div></a>
        <a :href="followURL" target="blank" send_referrer="false"><div id="seguir">Seguir</div></a>
        <a :href="masURL"
          ><div id="mas">
            <i class="fas fa-ellipsis-h"></i></div
        ></a>
      </div>
    </div>
  </div>
</template>

<script>
import { EventBus } from "./event-bus";
export default {
  data() {
    return {
      informacion: [],
      tituloCancion: "Bienvenido!",
      albumportada: '"../assets/imagenes/img250.png"',
      albumtitulo: "Busca la canción",
      artistanombre: "que tú quieras",
      artistafoto: '"../assets/imagenes/adele.jpg"',
      fullplayURL: "#",
      followURL: "#",
      masURL: "https://www.deezer.com/es",
      itemAlAzar: [],
      comentario:
        "Selecciona el tipo de búsqueda desde el menú, ingresa tu búsqueda y presiona ENTER. Obtendrás una preview de 30 segundos. para escuchar el clip completo, ver más canciones del artista o compartir, utiliza los botones bajo este mensaje",
    };
  },
  name: "FuDetalle",

  created() {
    this.cancionRandomInicial();
  },
  methods: {
  async  cancionRandomInicial() {
      //Nro de track al azar, sino, usar 105746588
      var numeroRamdom = Math.floor( Math.random() * (18230000 - 15000000) + 15000000);
      console.log(numeroRamdom);
      //consulta del presunto track
      await fetch(("https://deezerdevs-deezer.p.rapidapi.com/track/"+numeroRamdom), {
        method: "GET",
        headers: {
          "x-rapidapi-key":
            "19a9142c6emsh91209d66b6f4e5dp1107cfjsne54690d64d38",
          "x-rapidapi-host": "deezerdevs-deezer.p.rapidapi.com",
        },
      })
        .then((response) => response.json())
        .then((data) => {
          this.itemAlAzar = data;
        });
      /////////////////
      this.llenarinfo(this.itemAlAzar);
    },

    llenarinfo(informacion) {
      try {
        console.log("Mostrando informacion del clip seleccionado");

        this.tituloCancion = informacion.title_short;
        this.albumportada = informacion.album.cover_big;
        this.albumtitulo = informacion.album.title;
        this.artistanombre = informacion.artist.name;
        this.artistafoto = informacion.artist.picture_xl;
        this.fullplayURL = informacion.link;
        this.followURL = informacion.artist.link;
        this.masURL = "https://www.deezer.com/es";
        this.comentario =
          "Estás escuchando " +
          this.tituloCancion +
          " de " +
          this.artistanombre +
          ". Para escuchar ésta y más musica, crea un cuenta en www.deezer.com!";
      } catch (error) {
        console.log("Algo falló al llenar la info del clip" + error);
      }
    },
  },
  mounted() {
    EventBus.$on("mostrarinformacion", (informacionnueva) =>
      this.llenarinfo(informacionnueva)
    );
  },
  beforeDestroy() {
    EventBus.$off("mostrarinformacion", (informacionnueva) =>
      this.llenarinfo(informacionnueva)
    );
  },
};
</script>

<style>
a,
a:link,
a:visited,
a:active,
a:hover {
  text-decoration: none;
}
.detalle__informacion__fondo_color {
  position: absolute;
  width: 91%;
  height: 165px;
  top: 0;
  right: 4.5%;
  background: linear-gradient(0deg, rgba(167, 0, 0, 0.7), rgba(167, 0, 0, 0.7));
  opacity: 0.5;
}
.detalle__informacion__fondo_img {
  position: absolute;
  object-position: 0px 20%;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  z-index: 1;
  opacity: 0.5;
  transform: matrix(-1, 0, 0, 1, 0, 0);
  object-fit: cover;
}
#mas {
  display: inline;
  border: none;
  background: transparent;
  border-radius: 100px;
  width: 20px;
  height: 31px;
  font-size: 0.875em;
  padding: 5.5px 10px 7px 10px;
  color: #fff !important;
  cursor: pointer;
  margin-left: 10px;
}
#reproducir {
  display: inline;
  border: 1px solid #e86060;
  background: #e86060;
  border-radius: 100px;
  width: 100px;
  height: 31px;
  font-size: 0.875em !important;
  padding: 5.5px 22px 7px 22px;
  color: #fff !important;
  cursor: pointer;
}
#seguir {
  display: inline;
  background: transparent;
  border-radius: 100px;
  width: 100px;
  height: 31px;
  border: 1px solid #e86060;
  font-size: 0.875em;
  box-sizing: border-box;
  padding: 5.5px 38px 7px 38px;
  cursor: pointer;
  margin-left: 10px;
  color: #e86060 !important;
}
.detalle__informacion__botones {
  display: inline;
  position: absolute;
  width: 74%;
  top: 77%;
  right: 3%;
  background: transparent;
}
.biografia {
  width: 100%;
  font-style: normal;
  font-weight: normal;
  font-size: 0.75em;
  line-height: 1.125em;
  line-clamp: 2;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: wrap;
}
.biografia p {
  max-height: 2.25em; /*line-height por nº de líneas*/
  overflow: hidden;
  text-align: justify;
}
.biografia p ::after {
  content: "...";
  background: #ffeeaa;
  position: absolute;
  top: 1.125em; /*max-height menos 1 line-height*/
  right: 0;
}
.album {
  font-family: Quicksand;
  font-size: 1.375em;
  font-style: normal;
  font-weight: 700;
}
.comentario {
  display: inline-block;
  font-family: Quicksand;
  font-size: 0.875em;
  font-style: normal;
  font-weight: 400;
  line-height: 0.875em;
  margin-top: -0.75px;
}
.seguidores {
  display: inline-block;
  font-family: Quicksand;
  font-style: normal;
  font-weight: normal;
  font-size: 0.75em;
  line-height: 0em;
  text-indent: 1em;
  color: #662323;
}
.detalle__informacion {
  position: absolute;
  width: 91%;
  height: 165px;
  background: transparent;
  top: 0;
  right: 4.5%;
  z-index: 3;
}
.detalle__informacion__descripcion {
  position: absolute;
  width: 74%;
  height: 165px;
  background: transparent;
  top: 0;
  right: 3%;
  line-height: 0em;
  letter-spacing: 0em;
  text-align: left;
  color: #fff;
}
.detalle__informacion__imagen {
  position: absolute;
  width: 20%;
  height: 165px;
  background: transparent;
  top: 0;
  object-fit: cover;
}
/*.imagen {
  max-width: 200px;
  max-height: 200px;
}*/
.detalle__contenedor {
  position: fixed;
  top: 80px;
  right: 0px;
  width: 75%;
  height: 165px;
  background: #fff;
}

@media screen and (min-width: 701px) {
  /*VISTA PC*/
}
@media screen and (max-width: 700px) {
  /*VISTA MOVIL*/
  .detalle__contenedor {
    position: fixed;
    top: 116px;
    right: 0px;
    width: 100%;
  }
  .detalle__informacion__imagen {
    display: none;
  }
  .detalle__informacion__descripcion,
  .detalle__informacion__botones {
    width: 96%;
    right: 2%;
  }
  #reproducir,
  #seguir {
    width: 60px;
    padding: 5.5px 22px 7px 22px;
  }
  #mas {
    width: 30px;
  }
}
</style>