  computed: {},

  methods: {
    seleccionarOpcionDelMenu(seleccion) {
      this.opcionmenuseleccionada = seleccion;
      EventBus.$emit("cambiodeseleccionenelmenu", this.opcionmenuseleccionada);
      //console.log("se envió hacer busqueda de " + this.opcionmenuseleccionada);
    },
    estiloactivo(consulta) {
      return {
        menu__seleccionado: consulta == this.opcionmenuseleccionada,
      };
    },
  },
};
</script>

